.Main {
    padding: 10px;
    margin: 15px;
    max-width: 500px;
    background-color: greenyellow;
}

.Message {
    border: 2px solid darkgreen;
    background-color: darkgreen;
    border-radius: 10px 10px 10px 0;
    padding: 10px;
    margin-left: 80px;
    position: relative;
}

.Message:after {
    content: "";
    width: 0px;
    height: 0px;
    display: block;
    border-left: 5px solid transparent;
    border-right: 5px solid darkgreen;
    border-top: 5px solid transparent;
    border-bottom: 5px solid darkgreen;
    position: absolute;
    bottom: -2px;
    left: -10px;
}

.Image {
    float: left;
    max-width: 60px;
    width: 100%;
    margin-right: 20px;
    margin-top: 15px;
    border-radius: 50%;
}

.Name {
    color: #cf87f5;
}

.Time {
    float: right;
    color: #999;
    margin: 5px;
}

.Text {
    color: white;
}

.TypeText {
    margin: 15px;
    float: inherit;
}